aside.menu-area {
    background-color: var(--bg-dark);
}
.menu a {
    display: block;
    text-decoration: none;
    color: #fff;
    font-weight: 300;
    padding: 15px;
}
.menu a:hover {
    background: linear-gradient(135deg, #363535 0%, rgb(51, 49, 49) 100%);
}
@media(max-width: 768px) {
    .menu {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .menu a{
        display: inline;
        padding: 10px;
        margin: 0px;
    }
    .menu {
        height: 100%;
        justify-content: space-around;
    }
}